000px\r\n    width: 100%\r\n    gap: 16px\r\n    @include mix.doFlexBox(start, stretch, column)\r\n\r\n    .section-header\r\n      @include mix.doFlexBox()\r\n      .section-header-text\r\n        font-size: 1rem\r\n        color: var.$primary\r\n        @include mix.smoothTransition()\r\n        @media (min-width: var.$tablet)\r\n          font-size: 1.2rem\r\n\r\n    #restaurant-list-wrapper\r\n      display: grid\r\n      gap: 16px\r\n      grid-template-columns: repeat(auto-fill, minmax(280px, 1fr))\r\n\r\n      .restaurant-card\r\n        border: 1px solid var.$border\r\n        border-radius: 16px\r\n        overflow: hidden\r\n        @include mix.doFlexBox(start, stretch, column)\r\n\r\n        .restaurant-card-image\r\n          position: relative\r\n          overflow: hidden\r\n\r\n          .restaurant-location\r\n            position: absolute\r\n            left: 0\r\n            top: 0\r\n            background-color: var.$primary\r\n            color: var.$border\r\n            margin: 16px\r\n            font-weight: bold\r\n            padding: 4px 8px\r\n            border-radius: 999em\r\n            font-size: .8rem\r\n\r\n          img\r\n            width: 100%\r\n            aspect-ratio: 16/9\r\n            object-fit: cover\r\n\r\n        .restaurant-card-desc\r\n          padding: 16px\r\n          @include mix.doFlexBox(start, stretch, column)\r\n          gap: 2px\r\n\r\n          .restaurant-food-rating\r\n            font-size: .9rem\r\n            color: var.$dark\r\n            @include mix.doFlexBox(start)\r\n            gap: 2px\r\n            font-weight: bold\r\n\r\n            img\r\n              width: 16px\r\n\r\n          .restaurant-food-name\r\n            color: var.$primary\r\n            font-size: 1.1rem\r\n\r\n          .restaurant-food-desc\r\n            font-size: .9rem\r\n            color: var.$muted-foreground\r\n            overflow: hidden\r\n            display: -webkit-box\r\n            -webkit-box-orient: vertical\r\n            -webkit-line-clamp: 3\r\n\r\n  #categories-wrapper\r\n    max-width: 1000px\r\n    width: 100%\r\n    gap: 16px\r\n    @include mix.doFlexBox(start, stretch, column)\r\n\r\n    .section-header\r\n      @include mix.doFlexBox()\r\n      .section-header-text\r\n        font-size: 1rem\r\n        color: var.$primary\r\n        @include mix.smoothTransition()\r\n        @media (min-width: var.$tablet)\r\n          font-size: 1.2rem\r\n\r\n\r\n    #categories-list-wrapper\r\n      display: grid\r\n      gap: 16px\r\n      grid-template-columns: repeat(auto-fill, minmax(280px, 1fr))\r\n\r\n      .category-card\r\n        border: 1px solid var.$border\r\n        border-radius: 16px\r\n        overflow: hidden\r\n        padding: 12px\r\n        background-size: cover\r\n        background-position: center\r\n        @include mix.doFlexBox(start, stretch, column)\r\n\r\n        .category-title\r\n          color: var.$primary\r\n          font-size: 1.1rem\r\n\r\n        .category-desc\r\n          font-size: .9rem\r\n          color: var.$muted-foreground\r\n          overflow: hidden\r\n          display: -webkit-box\r\n          -webkit-box-orient: vertical\r\n          -webkit-line-clamp: 2","@mixin doFlexBox($justify-content: center, $align-items: center, $flex-direction: row)\r\n  display: flex\r\n  justify-content: $justify-content\r\n  align-items: $align-items\r\n  flex-direction: $flex-direction\r\n\r\n@mixin smoothTransition($duration: .3s)\r\n  transition: all ease-in-out $duration","// responsive\r\n$tablet: 768px\r\n$desktop: 1200px\r\n\r\n// color\r\n$primary: #2dd4bf //teal\r\n$border: #f1f1f1\r\n$dark: #494949\r\n$muted-foreground: #8f8f8f"],"sourceRoot":""}]);
